
import React, { useEffect, useState } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal,
    InputGroup,
    FormControl,
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';
import axios from 'axios';
var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, i18n.t('error-short-city'))
        .max(20,i18n.t('error-long-city'))
        .required(i18n.t('error-required-city')),
    gouvernorat: Yup.string()
        .min(2, i18n.t('error-short-governorate'))
        .max(20,i18n.t('error-long-governorate'))
        .required(i18n.t('error-required-governorate')),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
    address: Yup.string()
        .required(i18n.t('error-address')),
});
function UpdateZone(props) {
    const [show, setShow] = useState(false);
    const [zonesUpdate] = useState(props.zone)
    const [agences, setagences] = useState([])
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [showMessageParams, setShowMessageParams] = useState({
        display:false,
        type:"success",
        message:null
    })
    useEffect(() => {
        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios
            .get(urlbackend + "/agence", config)
            .then((response) => {
                console.log("this respon list", response.data);
                setagences(response.data.data)
                // this.setState({ agences: response.data.data });
            })
            .catch((e) => {
                console.log(e);
            });

    }, [])
    const handlSubmit = (values) => {
        setShowMessageParams({
            display:false,
            type:"success",
            message:null
        });
        console.log(values);

        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.put(urlbackend + "/zone/" + zonesUpdate.id, values, config)
            .then(response => {
                setShowMessageParams({
                    display:true,
                    type:"success",
                    message:i18n.t('success-update-zone')
                });
                console.log(response.data)
                props.change()
                handleClose()

            })
            .catch(e => {
                setShowMessageParams({
                    display:true,
                    type:"danger",
                    message:i18n.t('error-update-zone')
                });
                console.log(e)
            })
    };
    useEffect( () => {
        // console.log("show message value:",showMessage);
    }, [showMessageParams]);
    const showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5"></Card.Title>
                            </Card.Header>
                            {showMessageParams.display==true ?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null
                            }
                            <Card.Body>
                                <Row>
                                    <Col md={6}>





                                        {/* <CustomInput
                      name="password"
                      label="password"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.password}
                      errors={errors.password}
                      touched={touched.password}
                      type="password"
                    /> */}

                                        <CustomInput
                                            name="gouvernorat"
                                            label={i18n.t('governorate')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.gouvernorat}
                                            errors={errors.gouvernorat}
                                            touched={touched.gouvernorat}
                                        />
                                        <CustomInput
                                            name="city"
                                            label={i18n.t('city')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.city}
                                            errors={errors.city}
                                            touched={touched.city}
                                        />

                                    </Col>
                                    <Col md={6}>







                                        <label htmlFor="basic-url">{i18n.t('agency')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="agence"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.Formateur}
                                            >
                                                <option>{i18n.t('choose-agency')}</option>

                                                {agences.map((agence, index) => (
                                                    <option key={agence.id} value={agence.id}>
                                                        {agence.name}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>



                                        {/* <CustomInput
                      name="company"
                      label="company"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.company}
                      errors={errors.company}
                      touched={touched.company}
                    /> */}
                                        {/* <CustomInput
                      name="codeFiscal"
                      label="codeFiscal"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.codeFiscal}
                      errors={errors.codeFiscal}
                      touched={touched.codeFiscal}
                    /> */}

                                    </Col>
                                </Row>

                                <Button
                                    onClick={() => {
                                        handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };

    return (
        <>
            <Button variant="info"
                onClick={handleShow}> <span className="fa fa-pencil"></span></Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{i18n.t('update-zone')}</Modal.Title>
                </Modal.Header>
                <Modal.Body>

                    <Formik
                        initialValues={{
                            agence: `${zonesUpdate.agence.id}`,
                            city: `${zonesUpdate.city}`,
                            gouvernorat: `${zonesUpdate.gouvernorat}`,
                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            this.submitForm(values);
                            setSubmitting(false);
                        }}
                        validationSchema={SignupSchema}
                    >
                        {(props) => showForm(props)}
                    </Formik>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        {i18n.t('close')}
            </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default UpdateZone