import React from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { Link } from "react-router-dom"
import axios from 'axios'
//import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import Aux from "../../../hoc/_Aux";
import UpdateZone from './Update.Zone';
import i18n from './../../../i18n';

var urlbackend = require('../../../env/env')();


class ListZone extends React.Component {
    state = {
        Zones: [],
    };
  
    componentDidMount() {

     
        const config = {
            headers: {
              "Content-Type": "application/json",
              "Access-Control-Allow-Headers": "x-access-token",
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };
      
          axios
            .get(urlbackend + "/zone", config)
            .then((response) => {
              console.log("this respon list", response.data);
              this.setState({ Zones: response.data.data });
            })
            .catch((e) => {
              console.log(e);
            });

        
    }

    Delette(Zone){
        console.log("hellodelet", Zone.id)
 
    }
    render() {
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">   <Link to="/Add/zone" >  <Button>{i18n.t('add-zone')}</Button> </Link> </Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>{i18n.t('code')}</th>
                                            <th>{i18n.t('governorate')}</th>

                                            <th>{i18n.t('zone')}</th>
                                            <th>{i18n.t('address')}</th>

                                            
                                            <th>{i18n.t('agency')}</th>
                                            <th>{i18n.t('action-event')}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.Zones.map((Zone, index) => (

                                            <tr key={Zone.id} >
                                               
                                                <td>{Zone.name} </td>
                                                <td>{Zone.gouvernorat} </td>
                                                <td>{Zone.city} </td>
                                                <td>{Zone.address}  {Zone.city} </td>
                                                <td>{Zone.agence.name} </td>

                                                <td>  <Button variant="danger"
                                                    onClick={()=> this.Delette(Zone)}> <span className="fa fa-trash"></span></Button> 
                                                    
                                                    <UpdateZone zone={Zone} change={()=> this.componentDidMount()} />
                                                    </td>
                                            </tr>


                                        ))}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListZone;